import { WEATHER_CLASS } from './constants';
import render from './render';
import createWeatherCity from './weather';
import storage from './storage';

async function formHandler(event) {
  event.preventDefault();
  const input = event.target.querySelector(`.${WEATHER_CLASS.SEARCH_INPUT}`);
  const inputValue = input.value;
  if (inputValue === '') return;
  createWeatherCity(inputValue);
  input.value = '';
}

function cityHandler(event) {
  const { target } = event;
  if (target.className !== WEATHER_CLASS.ADD_CITY) return;
  const city = target.previousElementSibling.textContent;
  storage.addOrRemoveCity(city);
  render.allInfo();
}

function favouritesHandler(event) {
  const { target } = event;
  if (target.className === WEATHER_CLASS.FAVOURITES_CITY) { createWeatherCity(target.textContent); }

  if (target.className === WEATHER_CLASS.FAVOURITES_REMOVE) {
    const city = target.closest(`.${WEATHER_CLASS.FAVOURITES_CITY}`).textContent;
    storage.addOrRemoveCity(city);
    render.allInfo();
  }
}

function buttonHandler(event) {
  const button = event.target;
  render.changeActiveTab(button);
}

export {
  formHandler, buttonHandler, cityHandler, favouritesHandler,
};
