import { API_KEY, SERVER_URL } from './constants';
import render from './render';
import storage from './storage';

// eslint-disable-next-line consistent-return
async function createWeatherCity(inputValue) {
  // eslint-disable-next-line no-param-reassign
  if (!inputValue) inputValue = storage.getLastCity();
  // eslint-disable-next-line no-use-before-define
  const weather = await getResponse(inputValue, SERVER_URL.WEATHER);
  if (!weather) return render.showError('Город не найден');
  // eslint-disable-next-line no-use-before-define
  outputWeather(weather);
  // eslint-disable-next-line no-use-before-define
  const weatherForecast = await getResponse(inputValue, SERVER_URL.FORECAST);
  if (!weatherForecast) return render.showError('Город не найден');
  render.weatherForecast = weatherForecast;

  render.infoWindow();
}

// eslint-disable-next-line consistent-return
async function getResponse(city, url) {
  const urlWeather = `${url}?q=${city}&appid=${API_KEY}&units=metric&lang=ru`;
  const response = await fetch(urlWeather);
  if (response.ok) {
    const json = await response.json();
    return json;
  }
}

const addZero = (number) => (number < 10 ? `0${number}` : number);

function getDate(timezone, seconds) {
  const date = new Date(seconds * 1000);
  const diffTimezone = date.getTimezoneOffset() / 60 + timezone / 3600;
  const diffTime = (24 + date.getHours() + diffTimezone) % 24;
  const dateTime = {
    hours: addZero(diffTime),
    minutes: addZero(date.getMinutes()),
  };
  return `${dateTime.hours}:${dateTime.minutes}`;
}

function outputWeather({
  name,
  weather,
  timezone,
  // eslint-disable-next-line camelcase
  main: { temp, feels_like },
  sys: { sunrise, sunset },
}) {
  render.weatherNow = {
    name,
    weather,
    sunrise: getDate(timezone, sunrise),
    sunset: getDate(timezone, sunset),
    temperature: Math.round(temp),
    feelsLike: Math.round(feels_like),
  };
  storage.setLastCity(name);
}

export default createWeatherCity;
